import dnIcon from '@/components/dn-icon/dn-icon.vue'

export default {
	components: {
		dnIcon
	},
	data() {
		return {
			textareaInfo: '',
		};
	},
	methods: {
		bindTextAreaBlur: function (e) {
			console.log(e.detail.value)
		},
		// 提交
		handleSubmit() {
			if(this.textareaInfo) {
				uni.showLoading({
					title: '正在提交...',
					mask: true,
					success() {
						setTimeout(() => {
							uni.hideLoading()
							uni.showToast({
								icon: "success",
								title: '提交成功',
								duration: 1000
							});
							setTimeout(() => {
								uni.navigateBack({
									delta: 1  // 上一页
								})
							},1000)
						}, 1500)
					}
				})
			} else {
				uni.showToast({
					icon: "none",
					title: '退款理由信息不能为空',
					duration: 2000
				});
			}
		}
	}
}
